body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.app {
  position: relative;
}

.character {
  position: absolute;
  min-height: 200px;
  min-width: 180px;
}

.char-head {
  position: absolute;
  top: 9px;
  right: 32px;
}

.char-body {
  position: absolute;
  right: 0;
  bottom: 0;
}